.sky {
    position: absolute; top:0;
    height:90%;
    width:100%;
    z-index: 0;
}
.ground {
    position: absolute; bottom:0;
    height:20%;
    width:100%;
    z-index: 5;
}
.mntn1 {
    position: absolute;
    bottom: -22%;
    right: -2%;
    background-size: contain;
    width:100%; height:100%;
    z-index: 3;
}
.mntn2 {
    position: absolute;
    bottom: -27%;
    left: -8%;
    background-size: contain;
    width:100%; height:100%;
    z-index: 2;
}
.mntn3 {
    position: absolute; bottom:-9%;
    background-size: contain;
    width:100%; height:100%;
    z-index: 1;
}
#moon {
    position: absolute;
    top: 6%;
    left: 10%;
    background-size: contain;
    width: 5vw;
    height: 5vw;
    z-index: 5;
}
#hut {
    position: absolute;
    left: 42.5%; bottom: 13%;
    width: 15%; height: 37%;
    background-size: contain;
    z-index: 6;
}
#main-title {
    position: absolute;
    top: 27%;
    color: white;
    opacity: .95;
    left: 10%;
    z-index: 1;
}
#title {
    font-size: 5vw;
}
#subtitle {
    font-size: 2vw;
}

.under-dev {
    color: white;
    position: absolute;
    width: 98vw;
    left: 0;
    bottom: 0;
    font-size: 1vw;
    z-index: 10;
    text-align: right;
    padding: 1vw;
}


@media screen and (max-width: 850px) {
    #hut {
        bottom:8%;
    }
    #title {
        font-size: 8vw;
    }
    #subtitle {
        font-size: 4vw;
    }
    .under-dev {
        font-size: 2vw;
    }
}


@media screen and (max-width: 450px) {
    #hut {
        left: 35%;
        bottom:10%;
        width: 30%;
    }
    .mntn1 {
        bottom: -24%;
        right: -3%
    }
    .mntn3 {
        bottom: -17%;
    }
    #moon {
        width: 10vw;
        height: 10vw;
    }

    #main-title {
        top: 15%;
        left: 6%;
    }
    #title {
        font-size: 10vw;
    }
    #subtitle {
        font-size: 6vw;
        margin: 1vw;
    }
    .under-dev {
        font-size: 3vw;
    }
}


@media screen and (min-height: 1200px) {
    .mntn3 {
        bottom: -16%;
    }
    #hut {
        bottom:6%
    }
}